<template>
    <div class="view-container">
        <p style="margin-bottom:10px">
            复制到剪切板
        </p>
        <el-input v-model="value" ref="copyDom"></el-input>
        <p style="height: 10px;"></p>
        <el-button type="primary" @click="copy">Copy</el-button>
    </div>
</template>

<script setup>
import { ref, unref } from 'vue';
import { ElMessage } from 'element-plus'
const copyDom = ref(null)
const value = ref('')
const copy = ()=>{
    let result = unref(value)
    // let selectDom = unref(copyDom)
    if(!result){
        ElMessage.warning('请输入要拷贝的内容！')
        return false
    }else{
        // 被弃用
        // selectDom.select()
        // document.execCommand("Copy");
        // 新的
        navigator.clipboard.writeText(result).then(()=>{
            ElMessage.success('内容已复制到剪切板')
        })
        return true
    }
}
</script>

